@mixin blockShadows($direction: null) {
  @if $direction == top {
    box-shadow: inset 0 20px 20px -20px fade-out(black, 0.3);
  } @else if $direction == bot {
    box-shadow: inset 0 -20px 20px -20px fade-out(black, 0.3);
  } @else {
    box-shadow:
      inset 0 20px 20px -20px fade-out(black, 0.3),
      inset 0 -20px 20px -20px fade-out(black, 0.3);
  }
}

@mixin borderLineAnimation() {
  transition: border-color 200ms ease-out;

  &:hover {
    border-color: $color-b-three;
  }
}